import React from 'react';
import Icon from './Icon';

export default ({ by, children }) => (
  <blockquote>
    <Icon icon="quote-left" />
    {children}
    <Icon icon="quote-right" />
    <p className="byline">{by}</p>
    <style jsx>{`
      .byline {
        margin-top: .5rem;
        text-align: right;
        font-weight: 300;
        font-style: italics;
      }
    `}
    </style>
  </blockquote>
);



